'use client';

import { useState } from 'react';
import Link from 'next/link';
import Image from 'next/image';
import Navbar from '@/components/Navbar';
import Footer from '@/components/Footer';

// Dummy data for gallery items
const galleryItems = [
  {
    id: 1,
    title: 'صورة فنية بتقنية AI',
    category: 'صور',
    imageUrl: '/images/gallery/image1.jpg',
    providerName: 'أحمد محمد',
    providerId: 1,
  },
  {
    id: 2,
    title: 'توليد فيديو لمدينة مستقبلية',
    category: 'فيديو',
    imageUrl: '/images/gallery/video1.jpg',
    providerName: 'فاطمة حسن',
    providerId: 4,
  },
  {
    id: 3,
    title: 'تطبيق واجهة مستخدم ذكي',
    category: 'مواقع',
    imageUrl: '/images/gallery/app1.jpg',
    providerName: 'خالد عبد الله',
    providerId: 3,
  },
  {
    id: 4,
    title: 'مقال علمي حول الذكاء الاصطناعي',
    category: 'مقالات',
    imageUrl: '/images/gallery/article1.jpg',
    providerName: 'سارة علي',
    providerId: 2,
  },
  {
    id: 5,
    title: 'تصميم شخصيات لقصة مصورة',
    category: 'صور',
    imageUrl: '/images/gallery/image2.jpg',
    providerName: 'أحمد محمد',
    providerId: 1,
  },
  {
    id: 6,
    title: 'نموذج Comfy UI متخصص',
    category: 'موديلز',
    imageUrl: '/images/gallery/model1.jpg',
    providerName: 'فاطمة حسن',
    providerId: 4,
  },
];

const categories = [
  { id: 'all', name: 'الكل' },
  { id: 'صور', name: 'صور' },
  { id: 'فيديو', name: 'فيديو' },
  { id: 'صوت', name: 'صوت' },
  { id: 'مقالات', name: 'مقالات' },
  { id: 'أبحاث', name: 'أبحاث' },
  { id: 'مواقع', name: 'مواقع' },
  { id: 'تطبيقات', name: 'تطبيقات' },
  { id: 'موديلز', name: 'موديلز' },
  { id: 'comfy-ui', name: 'Comfy UI' },
];

export default function GalleryPage() {
  const [selectedCategory, setSelectedCategory] = useState('all');

  const filteredItems = selectedCategory === 'all'
    ? galleryItems
    : galleryItems.filter(item => item.category === selectedCategory);

  return (
    <>
      <Navbar />
      <div className="bg-gray-50 py-12">
        <div className="mx-auto max-w-7xl px-4 sm:px-6 lg:px-8">
          <div className="text-center">
            <h1 className="text-3xl font-bold tracking-tight text-gray-900 sm:text-4xl">
              معرض أعمال مقدمي الخدمات
            </h1>
            <p className="mt-3 text-xl text-gray-500 sm:mt-4">
              استعرض أفضل الأعمال التي تم إنشاؤها بواسطة مزودي خدمات الذكاء الاصطناعي
            </p>
          </div>

          {/* Categories */}
          <div className="mt-10 flex justify-center space-x-1 space-x-reverse overflow-x-auto pb-4">
            {categories.map((category) => (
              <button
                key={category.id}
                className={`rounded-full px-4 py-2 text-sm font-medium ${
                  selectedCategory === category.id
                    ? 'bg-indigo-600 text-white'
                    : 'bg-white text-gray-700 hover:bg-gray-100'
                }`}
                onClick={() => setSelectedCategory(category.id)}
              >
                {category.name}
              </button>
            ))}
          </div>

          {/* Gallery Grid */}
          <div className="mt-12 grid gap-8 sm:grid-cols-2 lg:grid-cols-3">
            {filteredItems.map((item) => (
              <div key={item.id} className="bg-white overflow-hidden rounded-lg shadow">
                <div className="relative h-48 w-full">
                  <Image
                    src={item.imageUrl}
                    alt={item.title}
                    fill
                    className="object-cover"
                  />
                </div>
                <div className="p-6">
                  <h3 className="text-lg font-semibold text-gray-900">{item.title}</h3>
                  <p className="mt-2 text-sm text-gray-500">
                    التصنيف: {item.category}
                  </p>
                  <div className="mt-4 flex items-center justify-between">
                    <div className="flex items-center">
                      <Link
                        href={`/service-request?provider=${item.providerId}`}
                        className="text-sm font-medium text-indigo-600 hover:text-indigo-500"
                      >
                        {item.providerName}
                      </Link>
                    </div>
                    <Link
                      href={`/service-request?provider=${item.providerId}`}
                      className="inline-flex items-center rounded-md border border-transparent bg-indigo-100 px-3 py-1 text-sm font-medium text-indigo-700 hover:bg-indigo-200"
                    >
                      طلب خدمة
                    </Link>
                  </div>
                </div>
              </div>
            ))}
          </div>

          {filteredItems.length === 0 && (
            <div className="mt-12 text-center">
              <p className="text-gray-500">لا توجد أعمال في هذا التصنيف حالياً</p>
            </div>
          )}
        </div>
      </div>
      <Footer />
    </>
  );
} 